//prospect_profile
export const prospect_profile_get =
`SELECT *
    FROM
    prospect_profile p
    WHERE
    p.PROSPECT_ID = :prospect_id
    AND ROWNUM <= 1`


export const prospect_profile_get_last_id = "select * from prospect_profile order by prospect_id desc LIMIT 1"

export const prospect_profile_delete = "delete from prospect_profile where prospect_id = :prospectId"

//prospect_idp_log
export const prospect_idp_log_get =
    "SELECT\n" +
    "    *\n" +
    "    FROM\n" +
    "    prospect_idp_log As p\n" +
    "WHERE\n" +
    "p.PROSPECT_ID = @prospectId\n" +
    "ORDER BY DATETIME(p.UPDATE_DATE) DESC LIMIT 1"

export const prospectIdpLogGet =
    "SELECT * FROM prospect_idp_log p WHERE p.PROSPECT_ID = :prospectId ORDER by  p.EXP_DATE DESC"

export const prospect_idp_log_delete = "delete from prospect_idp_log where prospect_id = :prospectId"

